
import { _decorator, Component, Node } from 'cc';
import { PoolManager } from './PoolManager';
const { ccclass, property } = _decorator;
 
@ccclass('StoneEnemySpawner')
export class StoneEnemySpawner extends Component {

    stoneTimeBetweenSpawn: number = Math.random() * 7 + 3;   
    stoneCurSpawnTime: number = 0;

    start () {
        // [3]
    }

    update (deltaTime: number) {
        
        this.stoneCurSpawnTime += deltaTime;
        
        //then spawn stone obstacles, then repeat
        if (this.stoneCurSpawnTime > this.stoneTimeBetweenSpawn)
        {
            PoolManager.Instance.createStoneEnemy(this.node);
            this.stoneCurSpawnTime = 0;
            //this.stoneTimeBetweenSpawn = Math.random() * 13 + 10;
        }
    }
}

